.panel {
    position: fixed;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    width: 57em;
    height: 47em;
    font-family: HeaderFont;
    letter-spacing: .1em;
    -webkit-text-stroke: 2px black;
}

.container {
    background-repeat: no-repeat;
    background-position: center;
    background-size: 80%;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.restartButton {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: 28.5em;
    margin-bottom: -9.2em
}

.closeButton {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: 64em;
    margin-bottom: -9.2em
}

.overlayBtnText {
    position: relative;
    filter: drop-shadow(0 0 .4em rgb(0, 0, 0));
    text-decoration: none;
    color: #E9E9B2;
    cursor: url(../../Assets/cursor1.cur), auto;
}

.overlayBtnText:hover {
    filter: drop-shadow(0 0 .4em rgb(255, 217, 0));
}

.overlayBtnText:active {
    scale: .95;
    cursor: url(../../Assets/cursor2.cur), auto;
}

.text {
    position: absolute;
    z-index: 2;
    top: 1.45em;
    left: 1.7em;
    font-size: 1.2em;
    font-family: HeaderFont;
    letter-spacing: 0;
    -webkit-text-stroke: 2px black;
}

.text2 {
    position: absolute;
    z-index: 2;
    top: 1.45em;
    left: 1.4em;
    font-size: 1.2em;
    font-family: HeaderFont;
    letter-spacing: 0;
    -webkit-text-stroke: 2px black;
}

.btn2 {
    width: 11em;
}